.careers-tophead {
  padding: 10.25rem 0 0 0;
  background-image: url("/dist/img/careers-hero-bg.png");
  background-repeat: no-repeat;
  background-position-x: 100%;

  .txt-head {
    margin-bottom: 2.5rem;
  }

  h1 {
    margin-top: 0px;
    max-width: 876px;
    font-size: 5rem;
    margin-bottom: 2.75rem;
    font-family: ranade;
  }

  .cnt-head {
    font-family: Aileron;
    font-size: 1.375rem;
    font-weight: 300;
    line-height: 2.1875rem;
    letter-spacing: 0.03em;
    text-align: left;
    max-width: 43.3125rem;
    margin-bottom: 6.9375rem;
  }

  .careers-hero-img {
    background-image: url("/dist/img/careers-hero-img.png");
    background-repeat: no-repeat;
    background-size: cover;
    width: 100%;
    min-height: 45.4375rem;
    background-size: cover;
    background-position-x: 50%;
  }
}

