    font-size: 21px;
    font-weight: 700;
    line-height: 25px;
    letter-spacing: 0em;
    text-align: left;
  }

  .process-parent {
    margin-bottom: 7.0625rem;
    p {
      font-family: Aileron;
      font-size: 17px;
      font-weight: 400;
      line-height: 26px;
