//  Talents All around

.talents-sec {
  padding: 119px 0 158px 0;
  background-color: #f2f2f2;

  .txt-head {
    margin-bottom: 1.875rem;
  }

  .all-around {
    display: flex;
    justify-content: space-between;

    @media (max-width: $screen-ml) {
      display: block;
    }
    align-items: center;
    margin-bottom: 6.625rem;

    h2 {
      max-width: 583px;
    }

    p {
      font-family: Aileron;
      font-size: 21px;
      max-width: 53.5rem;
    }
  }

  .product-card {
    background: $color-base;
    border-radius: 20px, 12px, 12px, 20px;
    display: flex;
    align-items: center;
    margin-bottom: 2.5rem;

    @media (max-width: $screen-md) {
      border-bottom-left-radius: 20px;
      border-bottom-right-radius: 20px;
    }

    @media (max-width: $screen-md) {
      display: block;
    }

    .prod-img {
      width: 27%;

      background-repeat: no-repeat;
      height: 16.125rem;
      background-size: cover;

      @media (max-width: $screen-md) {
        width: 100%;
        height: 13.125rem;
        border-top-right-radius: 20px;
      }
    }

    .img-onboarding {
      background-image: url("/dist/img/prod-onboarding.png");
    }

    .img-design {
      background-image: url("/dist/img/product-design.png");
    }

    .img-development {
      background-image: url("/dist/img/product-development.png");
    }

    .img-assurance {
      background-image: url("/dist/img/product-qa.png");
    }

    .prod-cnt {
      width: 73%;
      padding-left: 5.625rem;
      padding-right: 4.875rem;
      @media (max-width: $screen-md) {
        width: 100%;
        padding: 1rem;
      }
    }

    h3 {
      font-family: Ranade;
      font-size: 1.1875rem;
      font-weight: 500;
      line-height: 23px;
      letter-spacing: -0.01em;
      text-align: left;
      margin-bottom: 1.5rem;
    }
    p {
      font-family: Aileron;
      font-size: 1.0625rem;
      font-weight: 400;
      line-height: 1.625rem;
      letter-spacing: 0.01em;
      text-align: left;
      color: $color-content;
    }
  }
}

