// Benefits sec

.benefits-sec {
  background-image: url("/dist/img/benefits-bg.png");
  background-repeat: no-repeat;
  background-color: #f9efef;
  padding: 8.4375rem 0 9.0625rem 0;
  background-position-y: 65%;

  .txt-head {
    margin-top: 2.4375rem;
    margin-bottom: 1.375rem;
  }
  h2 {
    max-width: 21.25rem;
    @media (max-width: $screen-lg) {
      max-width: 40rem;
      margin-bottom: 2rem;
    }
  }

  .card-parent {
    display: flex;
    flex-wrap: wrap;
    gap: 30px;
    justify-content: flex-end;

    @media (max-width: $screen-ml) {
      justify-content: center;
    }
  }

  .benefits-card {
    max-width: 26.25rem;
    padding: 2.875rem 2.5rem 3.1875rem 2.5rem;
    background-color: $color-base;
    box-shadow: 0px 8px 16px 0px #b01f241f;
    border-radius: 20px;

    .txt-head {
      margin-bottom: 1.375rem;
    }
    .benefits-img {
      margin-bottom: 2.5rem;
    }
    .benefits-cnt {
      h3 {
        font-family: Ranade;
        font-size: 1.1875rem;
        font-weight: 500;
        line-height: 1.4375rem;
        letter-spacing: -0.01em;
        text-align: left;
        margin-bottom: 1rem;
      }

      p {
        font-family: Aileron;
        font-size: 1.1875rem;
        font-weight: 300;
        line-height: 1.875rem;
        letter-spacing: 0.03em;
        text-align: left;
      }
    }
  }
}

// Review sec

.review-sec {
  padding: 7.875rem 0 8.75rem 0;
  text-align: center;

  .txt-head {
    margin-bottom: 1.375rem;
  }

  .top-cnt {
    margin-bottom: 6.75rem;
  }

  h2 {
    max-width: 43.625rem;
    margin: 0 auto;
    margin-bottom: 2rem;
    @media (max-width: $screen-sm) {
      font-size: 48px;
    }
  }

  p {
    max-width: 43.625rem;
    margin: 0 auto;
  }

  .review-slider-card {
    box-shadow: 0px 8px 32px 0px #00000014;
    background-color: $color-base;
    padding: 2.6875rem 3rem 3.125rem 3rem;
    max-width: 610px;
    border-radius: 2rem;
    margin: 20px 20px;
  }
  .rating-img {
    display: flex;
    margin-bottom: 1.875rem;
  }

  .profile-div {
    align-items: center;
    gap: 16px;
    justify-content: flex-start;
    margin-bottom: 1.625rem;

    h3 {
      font-family: Ranade;
      font-size: 1.1875rem;
      font-weight: 500;
      line-height: 1.4375rem;
      letter-spacing: -0.01em;
      text-align: left;
      margin-bottom: 5px;
    }

    h4 {
      font-family: Aileron;
      font-size: 0.8125rem;
      font-weight: 600;
      line-height: 1.375rem;
      letter-spacing: 0.01em;
      text-align: left;
    }
  }

  .card-cnt {
    p {
      font-family: Aileron;
      font-size: 0.9375rem;
      font-weight: 400;
      line-height: 1.625rem;
      letter-spacing: 0.01em;
      text-align: left;
    }
  }
}

.slick-slide {
  img {
    margin: 0;
  }
}
