@font-face {
  font-family: "Ranade";
  src: url(./../fonts/ranade/ranade-regular.woff2) format("woff2"),
    url(./../fonts/ranade/ranade-regular.woff) format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: "Ranade";
  src: url(./../fonts/ranade/ranade-medium.woff2) format("woff2"),
    url(./../fonts/ranade/ranade-medium.woff) format("woff");
  font-weight: 500;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: "Aileron";
  src: url(./../fonts/aileron/aileron-light-webfont.woff2) format("woff2"),
    url(./../fonts/aileron/aileron-light-webfont.woff) format("woff");
  font-weight: 300;
  font-style: normal;
  font-display: swap;
}

* {
  box-sizing: border-box;
}

body {
  margin: 0;
  height: 100%;
  font-size: 16px;
  background: #fff;
  color: #000;
}

$brand-dark: #450000;

main {
  display: block;
}

html {
  //   font-family: Matter, sans-serif;
  //   font-size: 2.66vw;
  height: 100%;
  text-align: left;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  -webkit-text-size-adjust: 100%;
}

h1 {
  font-size: 5.25rem;
  margin: 0.67em 0;
  color: $brand-dark;
  font-style: normal;
  font-weight: 400;
  line-height: 104%;
  letter-spacing: -4px;
  @media (max-width: $screen-sm) {
    font-size: 3.25rem;
  }
}

.text-highlight {
  color: $success;
}

.desc-highlight {
  color: $brand-dark;
  font-weight: 400;
}

.txt-head {
  color: #746a6a;
  font-family: Aileron;
  font-size: 0.9375rem;
  font-style: normal;
  font-weight: 700;
  line-height: 150%;
  letter-spacing: 0.3px;
  text-transform: uppercase;
  // text-align: center;
}

p {
  font-family: "Aileron", sans-serif;
  color: $color-black-text;
  font-size: 1.375rem;
  font-style: normal;
  font-weight: 300;
  line-height: 160%;
  letter-spacing: 0.0413rem;
}

h2 {
  color: $brand-dark;
  font-family: Ranade;
  font-size: 3.5rem;
  font-style: normal;
  font-weight: 300;
  line-height: 108.9%;
  letter-spacing: -3.36px;
}
h3 {
  color: $brand-dark;
  font-family: Aileron;
  font-size: 1.3125rem;
  font-style: normal;
  font-weight: 600;
  line-height: 120%; /* 25.2px */
}

blockquote,
h1,
h2,
h3,
h4,
h5,
h6,
nav {
  font-family: "Ranade", sans-serif;
}

@media (min-width: 768px) {
  //   html {
  //     font-size: 0.625vw;
  //   }
}

@media (min-width: 1920px) {
  //   html {
  //     font-size: 12px;
  //   }
}

@media print {
  blockquote,
  h1,
  h2,
  h3,
  h4,
  h5,
  h6,
  p {
    page-break-after: avoid;
    orphans: 3;
    widows: 3;
  }
}

View .th-layout {
  height: 100%;
}

.th-content {
  height: 100%;
}

// nav link

.navbar-nav .nav-link.active,
.navbar-nav .nav-link.show {
  color: $success;
}

// Ripple button

.button-wrapper {
  justify-content: center;
  align-items: center;
}
.btn-link {
  display: inline;
}
.btn-link-cta {
  position: relative;
  display: inline-block;
  height: auto;
  margin: 0;
  padding: 0;
  color: inherit;
  background: 0 0;
  border: 0;
  border-radius: 0;
  font-family: inherit;
  font-size: inherit;
  font-weight: inherit;
  letter-spacing: inherit;
  line-height: inherit;
  text-align: inherit;
  text-transform: inherit;
  text-indent: inherit;
  text-decoration: none;
}
.btn-link-cta:hover,
.btn-link-cta:focus {
  text-decoration: none;
}
.btn-link-cta {
  overflow: hidden;
  padding: 1.4rem 2rem;
  transform: translateZ(0);
  border-radius: 1000px;
  font-weight: 500;
  font-size: 1.4rem;
  line-height: 110%;
  text-transform: uppercase;
  transition: transform 0.3s;
}
.btn-link-cta {
  padding: 0 1.5rem;
  font-family: "Ranade", sans-serif;
  font-weight: 400;
  font-size: 105%;
  line-height: 0.92;
  height: 6rem;
  color: $success;
}
.btn-link-cta.-xxl {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 2.5rem;
  font-weight: 500;
  font-size: 0.9375rem;
  text-transform: none;
}
.btn-border {
  content: "";
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 1px;
  z-index: 3;
  border: 1px solid $success;
  border-radius: 1000px;
}
.btn-ripple {
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 1;
  overflow: hidden;
  transform: translateZ(0);
  border-radius: inherit;
}
.btn-ripple span {
  display: block;
  width: 100%;
  height: 100%;
  transform: translateY(101%);
  background-color: $success;
  border-radius: 50% 50% 0 0;
  transition: transform 0.5s cubic-bezier(0.4, 0, 0, 1),
    border-radius 0.5s cubic-bezier(0.4, 0, 0, 1);
}
.cb-btn_cta-title {
  position: relative;
  display: block;
  padding: 0 0.16em 0 0;
  overflow: hidden;
  z-index: 2;
}
.btn-title {
  position: relative;
  display: block;
  padding: 0 0.16em 0 0;
  overflow: hidden;
  z-index: 2;
}
.btn-title span {
  display: block;
  transition: transform 0.8s cubic-bezier(0.16, 1, 0.3, 1);
}
.btn-title span:after {
  content: attr(data-text);
  display: block;
  position: absolute;
  top: 110%;
  left: 0;
  color: #fff;
}

.main-theme-btn {
  text-decoration: none;
  padding: 14px 45px;
  background: #b01f24;
  box-shadow: 0px 2px 12px rgba(0, 0, 0, 0.32);
  border-radius: 4px;
  font-weight: 600;
  font-size: 1rem;
  line-height: 150%;
  letter-spacing: -0.02em;
  text-align: center;
  color: #fff;
  border: none;
  display: inline-block;
  cursor: pointer;
  font-family: Aileron;
}

@media (pointer: fine) {
  .btn-link-cta:hover .btn-title span {
    transform: translateY(-110%);
    color: $success;
  }
}

@media (pointer: fine) {
  .btn-link-cta:hover .btn-ripple span {
    border-radius: 0;
    transform: translateY(0);
    transition-duration: 0.5s, 0.9s;
  }
}

@media (min-width: 768px), (orientation: landscape) {
  .btn-border {
    border-width: 1px;
  }
}

@media (pointer: fine) {
  .btn-link-cta:hover {
    transform: scaleX(1.02);
    transition: transform 0.6s cubic-bezier(0.34, 5.56, 0.64, 1);
  }
}
@media (min-width: 768px), (orientation: landscape) {
  .btn-link-cta {
    font-size: 2.4rem;
  }
}
