// Hero section
.abt-tophead {
  padding: 10.3125rem 0px 0px 0px;
  background-image: url("/dist/img/abt-hero-bg.webp ");
  background-repeat: no-repeat;
  background-position-y: 0%;

  h1 {
    max-width: 58.6875rem;
    text-align: center;
    margin: 0 auto;
    margin-bottom: 2.75rem;
    font-family: Aileron;
  }
  p {
    max-width: 46.25rem;
    margin: 0 auto;
    text-align: center;
    color: rgba(116, 106, 106, 0.83);
    margin-bottom: 10.125rem;
  }
  .abt-hero-img {
    background-image: url("/dist/img/abt-hero-img.webp");
    background-repeat: no-repeat;
    width: 100%;
    height: 45.4375rem;
    background-size: cover;
  }
}

.our-mission-sec {
  padding: 8.5rem 0px 8.5rem 0px;

  h2 {
    margin-bottom: 2.8125rem;
    text-align: left;
  }
  p {
    color: rgba(116, 106, 106, 0.83);
    font-family: Aileron;
    max-width: 600px;
  }
  .head-sec {
    p {
      color: #746a6a;
      font-family: Aileron;
      font-size: 0.9375rem;
      font-style: normal;
      font-weight: 700;
      line-height: 150%;
      letter-spacing: 0.3px;
      text-transform: uppercase;
    }
  }
}

// Leadership Section

.leadership-sec {
  padding: 8.5rem 0px 8.5rem 0px;
  background: rgba(242, 242, 242, 1);

  .head-sec {
    justify-content: space-between;
    margin-bottom: 9.5rem;

    h2 {
      max-width: 393px;
      text-align: left;
    }
    p {
      max-width: 590px;
      font-family: Aileron;
    }
  }

  .ceo-details {
    display: flex;
    background: $color-base;
    border-radius: 20px;
    margin-bottom: 6.75rem;

    @media (max-width: $screen-md) {
      flex-direction: column;
    }
    .ceo-image {
      border-radius: 20px;
      background-image: url("/dist/img/ceo-image.webp");
      background-size: cover;
      background-repeat: no-repeat;
      width: 46%;

      @media (max-width: $screen-md) {
        height: 35vh;
        width: 100%;
      }

      @media (min-width: $screen-md) {
        width: 60%;
        background-position-x: 40%;
      }

      // @media (max-width: $screen-md) {
      //   height: 50vh;
      //   width: 100%;
      //   background-position-y: 14%;
      // }
    }
    .ceo-content {
      padding: 4.75rem 6.125rem 5.6875rem 6.25rem;
      h3 {
        max-width: 413px;
        margin-bottom: 0.875rem;
      }
      .designation {
        color: #746a6a;
        font-family: Aileron;
        font-size: 0.9375rem;
        font-style: normal;
        font-weight: 700;
        line-height: 150%;
        letter-spacing: 0.3px;
        text-transform: uppercase;
        margin-bottom: 2.5rem;
      }
      p {
        color: #746a6a;
        font-family: Aileron;
        font-size: 1.0625rem;
        font-style: normal;
        font-weight: 400;
        line-height: 150%;
        letter-spacing: 0.17px;
        max-width: 502px;
      }
    }
  }

  .member-details {
    img {
      margin-bottom: 2.6875rem;
    }
    .member-cnt {
      font-family: Aileron;
      font-weight: 400;
      font-size: 1.0625rem;
      line-height: 22.5px;
      letter-spacing: 1%;
    }
  }
}

// Ventures Section

.ventures-sec {
  background-color: $brand-dark;
  padding: 7.3125rem 0 9.1875rem 0;
  .venture-cnt {
    margin-bottom: 7.8125rem;
  }

  .txt-head {
    font-family: Aileron;
    margin-bottom: 1.375rem;
    text-align: center;
    color: #ffffffa8;
    letter-spacing: 2%;
  }
  h2 {
    color: $color-base;
    margin-bottom: 29px;
    font-weight: 300;
    text-align: center;
  }
  .venture-details {
    max-width: 958px;
    text-align: center;
    margin: 0 auto;
    color: #ffffffa8;
    letter-spacing: 1%;
    font-size: 1.3125rem;
    line-height: 2.1rem;
  }
  .member-details {
    img {
      margin-bottom: 2.6875rem;
    }
    .cnt-details {
      h3 {
        color: $color-base;
        font-family: Aileron;
        font-size: 1.3125rem;
        font-weight: 600;
        line-height: 25px;
        letter-spacing: 0em;
        text-align: center;
        margin-bottom: 0.75rem;
      }
      p {
        color: $color-base;
        font-family: Aileron;
        font-size: 0.9375rem;
        font-weight: 700;
        line-height: 23px;
        letter-spacing: 0.02em;
        text-align: center;
      }
    }
  }
}
