.hero-content {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  height: 100%;
  padding-top: 5rem;
  max-width: 992px;
  p {
    max-width: 764px;
  }
  // @media (min-width: $screen-sm) {
  //   padding-top: 0;
  // }
}

.th-tophead {
  background-image: url("/dist/img/hero-sec-bg.png");
  background-repeat: no-repeat;
  background-position-x: 100%;
  position: relative;
  height: 92rem;
  z-index: 10;

  @media (max-width: $screen-sm) {
    height: 100rem;
  }

  @media (min-width: $screen-sm) {
    min-height: 50vh;
  }
}

// .gs_reveal {
//   opacity: 0;
//   visibility: hidden;
//   will-change: transform, opacity;
// }

.spiral {
  position: absolute;
  top: 14%;
  right: -86%;
}

.th-hero-vid {
  position: relative;
  height: 565px;
  background-size: contain;
  background-repeat: no-repeat;
  width: 100%;
  margin-top: 4rem;
  overflow: hidden;
  .th-overlay {
    position: absolute;
    left: 0;
    right: 0;
    bottom: 0;
    top: 0;
    width: 100%;
    height: 100%;
    border-radius: 32px;
    background: #b01f24;
    mix-blend-mode: darken;
  }
}
//slider

.row2 {
  margin-top: 3.4375rem;
  background: #fff;
  mix-blend-mode: darken;
  box-shadow: 0px 0px 64px 0px rgba(0, 0, 0, 0.08);
  border-radius: 2rem;
  .think201-icons-slider {
    height: 8.5625rem;
    flex-shrink: 0;
    display: flex;
    align-items: center;
    background: linear-gradient(90deg, #fff 0%, rgba(255, 255, 255, 0) 209.27%);
  }
}

.slick-slide {
  img {
    margin: auto;
  }
}

//Expertise section

.expertise {
  background-color: #f2f2f2;
  padding: 6.9375rem 0px 9.4375rem 0;

  .head-sec {
    .txt-head {
      margin-bottom: 1.375rem;
      text-align: center;
    }
    h2 {
      max-width: 52.875rem;
      margin: 0 auto;
      text-align: center;
      margin-bottom: 6.3125rem;
      @media (max-width: $screen-sm) {
        font-size: 2.5rem;
      }
    }
  }

  .expertise-details {
    img {
      margin-bottom: 2.625rem;
      max-width: 413px;
      @media (max-width: $screen-sm) {
        max-width: 360px;
      }
    }

    h3 {
      font-family: Aileron;
      font-weight: 700;
    }
    p {
      color: $color-content;
      font-family: Aileron;
      font-size: 1.0625rem;
      font-style: normal;
      font-weight: 400;
      line-height: 150%;
      letter-spacing: 0.0106rem;
      max-width: 413px;
    }
  }
}

//testimonial section

.testimonial {
  padding: 5.625rem 0;
  background-color: $brand-dark;

  .cmnt-parent {
    max-width: 51.3125rem;
    margin-left: 6.5rem;
    padding-top: 2.8125rem;
    @media (max-width: $screen-lg) {
      margin-left: 0rem;
    }

    .comment {
      color: $color-base;
      font-family: Ranade;
      font-size: 1.875rem;
      font-style: normal;
      font-weight: 400;
      line-height: 118%; /* 35.4px */
      letter-spacing: -0.0938rem;
      margin-bottom: 1.75rem;

      @media (max-width: $screen-md) {
        font-size: 1.275rem;
      }
    }

    .name {
      color: $color-base;
      font-family: Aileron;
      font-size: 1.3125rem;
      font-style: normal;
      font-weight: 700;
      line-height: 120%;
      text-align: left;
      margin-bottom: 0.5rem;
    }
    .designation {
      color: $color-base;
      font-family: Aileron;
      font-size: 0.9375rem;
      font-style: normal;
      font-weight: 400;
      line-height: 150%;
      letter-spacing: 0.0094rem;
    }
  }
}

//Industries we have contributed towards section

.industries-contributed {
  background-color: $color-base;
  padding: 9.625rem 0 7.5625rem 0;
  .head-sec {
    justify-content: space-between;
    margin-bottom: 6.75rem;
  }

  h2 {
    max-width: 30.4375rem;
    color: $brand-dark;
    letter-spacing: -0.21rem;
  }
  p {
    color: $color-content;
    font-family: Aileron;
    font-size: 1.3125rem;
    letter-spacing: 0.0394rem;
    max-width: 40.75rem;
  }
  .sector-text {
    color: $color-base;
    text-align: center;
    text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    font-family: Ranade;
    font-size: 1.5rem;
    font-style: normal;
    font-weight: 700;
    line-height: 120%;
  }
  .sector-box1 {
    padding: 15.875rem 0px;
    @media (max-width: $screen-sm) {
      padding: 7.4375rem 10rem 7rem 10.0625rem;
    }
    border-radius: 12px;
    display: flex;
    justify-content: center;
    align-items: center;
    background: linear-gradient(
        0deg,
        rgba(0, 0, 0, 0.4) 0%,
        rgba(0, 0, 0, 0.4) 100%
      ),
      url("/dist/img/social-sector.webp"), lightgray 50%;
    background-repeat: no-repeat;
    background-size: cover;

    @media (max-width: $screen-sm) {
      background-position-y: 58%;
    }
  }

  .sector-box2 {
    padding: 7.4375rem 10rem 7rem 10.0625rem;
    border-radius: 12px;
    background: linear-gradient(
        0deg,
        rgba(0, 0, 0, 0.4) 0%,
        rgba(0, 0, 0, 0.4) 100%
      ),
      lightgray 50%;
    background-repeat: no-repeat;
    background-size: cover;
    display: flex;
    align-items: center;
    justify-content: center;
  }

  .climate-sec {
    background-image: url("/dist/img/climate.webp");
  }
  .sass-sec {
    background-image: url("/dist/img/sass.webp");
  }
  .educational-sec {
    background-image: url("/dist/img/educational.webp");
  }
  .finance-sec {
    background-image: url("/dist/img/finance.webp");
  }
}

// Impact we have created section

.impact-creted-sec {
  background-color: #f9efef;
  padding: 7.25rem 0 8rem 0;

  .head-sec {
    display: flex;
    justify-content: space-between;
    @media (max-width: $screen-sm) {
      display: block;
    }
    margin-bottom: 3.25rem;

    h2 {
      letter-spacing: -0.21rem;
      max-width: 25.8125rem;
    }
    a {
      color: #b01f24;
      max-width: 14.8125rem;
      text-align: right;
      font-family: Aileron;
      font-size: 1.3125rem;
      font-style: normal;
      font-weight: 400;
      line-height: 160%; /* 33.6px */
      letter-spacing: -0.21px;
      text-decoration-line: underline;
      cursor: pointer;
    }
  }
  .impact-created-box {
    border-radius: 12px;
    padding: 8.25rem 1.5rem 1.5rem 1.5rem;
    background-size: cover;
    background-repeat: no-repeat;
  }

  .stats-box {
    padding: 1.75rem 4.25rem 1.25rem 1.5rem;
    background: $color-base;
    border-radius: 12px;
    opacity: 0.93;
    box-shadow: 0px 4px 32px 0px rgba(0, 0, 0, 0.25);

    h3 {
      color: #1e1e1e;
      font-family: Ranade;
      font-size: 3.5rem;
      font-style: normal;
      font-weight: 400;
      line-height: 108.9%;
      letter-spacing: -0.14rem;
    }
    p {
      color: #1e1e1e;
      font-family: Aileron;
      font-size: 0.875rem;
      font-style: normal;
      font-weight: 400;
      line-height: 150%;
      letter-spacing: 0.0175rem;
    }
  }
  .box-1 {
    background-image: url("/dist/img/box1.webp");
  }
  .box-2 {
    background-image: url("/dist/img/box2.webp");
  }

  .box-3 {
    background-image: url("/dist/img/box3.webp");
  }
}

// Work together section css

.work-together-sec {
  background-color: #f9f9f9;
  padding: 8.9375rem 0 6.9375rem 0;
  background-image: url("/dist/img/work-together-bg.png");
  background-repeat: no-repeat;
  background-position-x: 1%;
  background-position-y: 26%;

  h2 {
    text-align: center;
    letter-spacing: -0.21rem;
    max-width: 53.125rem;
    margin: 0 auto;
    margin-bottom: 1.25rem;
  }
  p {
    color: $color-content;
    max-width: 41.5rem;
    margin-bottom: 2.8125rem;
    text-align: center;
    margin: 0 auto;
    margin-bottom: 2.8125rem;
    font-family: Aileron;
  }
}
