// Override Bootstrap's Sass default variables
//
// Nearly all variables in Bootstrap are written with the `!default` flag.
// This allows you to override the default values of those variables before
// you import Bootstrap's source Sass files.
//
// Overriding the default variable values is the best way to customize your
// CSS without writing _new_ styles. For example, you can either change
// `$body-color` or write more CSS that override's Bootstrap's CSS like so:
// `body { color: red; }`.

//
// Bring in Bootstrap
//

// Option 1
//
// Import all of Bootstrap's CSS

// @import "bootstrap/scss/bootstrap";

// Option 2
//
// Place variable overrides first, then import just the styles you need. Note that some stylesheets are required no matter what.

// Toggle global options
$enable-gradients: true;
$enable-shadows: true;

$offcanvas-box-shadow: 0 1rem 3rem rgba(0, 0, 0, 0.175);

// Include functions first
@import "bootstrap/scss/functions";

// Customize some defaults
$body-color: #8c8383;
$body-bg: #fbfbf7;
$border-radius: 0.4rem;
$success: #b01f24;

// btn var overrides
$btn-border-radius: 40px;
$btn-border-width: 1.5px;
$btn-box-shadow: 0px 8px 14px 0px rgba(186, 49, 54, 0.08);
$btn-focus-width: 2px;
$btn-padding-x: 22px;
$btn-padding-y: 7px;
$btn-font-size: 0.9375rem;

// nav var overrides
$nav-link-font-size: 0.9375rem;
$nav-link-color: #1e1e1e;
$nav-link-font-weight: 500;
// $nav-link-hover-color: $success;
$nav-tabs-link-active-color: #b01f24;
$nav-tabs-link: #450000;
$nav-tabs-link-font-size: 1.1875rem;
$nav-tabs-link-hover-color: #b01f24;
$nav-tabs-padding-x: 4rem;
// $nav-tabs-link-active-border-color: #f2f2f2;
// $nav-tabs-link-hover-border-color: none;
// $nav-tabs-background-color: #f2f2f2;

// Accordion

$accordion-width: 100%;
$accordion-button-icon: none;
$accordion-button-active-icon: none;
$accordion-bg: #ffffff;
$accordion-border-radius: 20px;
$accordion-inner-border-radius: 20px;
$accordion-button-padding-x: 48px;
$accordion-button-padding-y: 24px;
$accordion-button-active-color: none;
$accordion-button-active-bg: none;
$accordion-button-focus-box-shadow: none;
$accordion-button-focus-border-color: none;
$accordion-button-color: none;
$accordion-button-color-border-color: none;

