// Hero-Section

.case-studies-tophead {
  padding-top: 9.6875rem;
  background-image: url("/dist/img/cst-hero-bg.webp ");
  background-repeat: no-repeat;
  background-position-x: 100%;

  h1 {
    margin-bottom: 2.75rem;
    font-size: 5rem;
    font-family: Ranade;
    line-height: 83px;
    letter-spacing: -0.06em;
    text-align: left;
    margin-top: 0;
  }
  p {
    font-family: Aileron;
    letter-spacing: 0.03em;
    text-align: left;
    margin-bottom: 5.25rem;
    max-width: 564px;
  }
  .cst-wrapper {
    background-color: #f2f2f2;
    border-radius: 24px;
    margin-bottom: 5.25rem;
  }
  .tab-content {
    margin-top: 5.25rem;
    margin-bottom: 10.25rem;
  }

  .cst-details {
    img {
      margin-bottom: 2rem;
    }

    .txt-head {
      margin-bottom: 1.0625rem;
    }
    h2 {
      max-width: 413px;
      font-family: Aileron;
      font-size: 1.3125rem;
      font-weight: 700;
      line-height: 1.5625rem;
      letter-spacing: 0em;
      text-align: left;
      margin-bottom: 1.3125rem;
    }
    .cst-content {
      font-family: Aileron;
      font-size: 1.0625rem;
      font-weight: 400;
      line-height: 1.625rem;
      letter-spacing: 0.01em;
      text-align: left;
      max-width: 413px;
    }
  }
  .nav-tabs {
    border-bottom: none;
    align-items: center;
    padding: 0 2rem;
    @media (max-width: $screen-lg) {
      padding: 3rem 2rem;
    }
  }
  .nav-item {
    // padding: 0px;
    padding: 0rem 2rem;
    @media (max-width: $screen-lg) {
      padding: 1rem 2rem;
    }
    border: none;
    .active {
      border: none;
      border-bottom: 3px solid #b01f24;
      background-color: #f2f2f2;
      padding: 2.25rem 0px;
      @media (max-width: $screen-lg) {
        padding: 0px 0px;
      }
      font-family: Ranade;
      font-size: 1.1875rem;
      font-weight: 500;
      line-height: 1.4375rem;
      letter-spacing: -0.03em;
      text-align: left;
    }
  }
  .nav-link:hover {
    border-color: none;
  }
  .nav-link {
    color: $brand-dark;
    font-size: 1.1875rem;
    border: none;
    font-family: Ranade;
    font-weight: 400;
    line-height: 1.4375rem;
    letter-spacing: -0.03em;
    text-align: left;
    padding: 0px;
  }
  a {
    text-decoration: none;
  }
}

.cst-work-together {
  background-color: #f2f2f2;
}
