.services-tophead {
  padding-top: 2.125rem;
  background-image: url("/dist/img/services-bg.png");
  background-repeat: no-repeat;
  background-size: cover;
  .services-hero-content {
    padding: 10.25rem 0rem 6.9375rem 0rem;
  }
  .services-overlay {
    background-image: url("/dist/img/bg-overlay.png");
  }

  .txt-head {
    color: #ffff;
    margin-top: 0px;
    margin-bottom: 2.5rem;
  }

  h1 {
    max-width: 800px;
    font-style: ranade;
    margin-bottom: 2.75rem;
    color: #ffffff;
  }
  .hero-cnt {
    max-width: 37.9375rem;
    color: #ffffffd4;
  }
}

//  Our Process Section

.process-sec {
  padding: 6.8125rem 0px 10.1875rem 0px;
  .txt-head {
    text-align: center;
    margin-bottom: 1.375rem;
  }

  h2 {
    text-align: center;
    margin-bottom: 2.5rem;
  }
  .process-cnt {
    max-width: 43.625rem;
    text-align: center;
    margin: 0 auto;
    margin-bottom: 5.5rem;
    color: $color-black-text;
  }
}
.process-parent {
  .process-img {
    background-repeat: no-repeat;
    background-size: cover;
    min-height: 286px;
    margin-bottom: 2.625rem;
    padding-top: 20px;
    padding-left: 20px;
  }

  .rsrch-dvlp {
    background-image: url("/dist/img/research&development.png");
  }
  .dsn-process {
    background-image: url("/dist/img/design-process.png");
  }

  .bcknd-dvlp {
    background-image: url("/dist/img/back-end-development.png");
  }

  .frnt-end-dev {
    background-image: url("/dist/img/front-end.png");
  }

  .quality-assurance {
    background-image: url("/dist/img/quality-assurance.png");
  }

  .deployment {
    background-image: url("/dist/img/deployment.png");
  }

  .num-crd {
    height: 64px;
    width: 64px;
    background-color: #ffffff;
    box-shadow: 0px 16px 16px 0px #00000040;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 12px;

    h3 {
      color: $brand-dark;
      font-size: 32px;
      font-family: ranade;
      font-weight: 400;
    }
  }

  p {
    font-family: Aileron;
    font-size: 17px;
    font-weight: 400;
    line-height: 26px;
    letter-spacing: 0.01em;
    text-align: left;
    color: $color-content;
  }
}
//  Technology We Use

.tech-stack {
  padding: 7.5625rem 0 9.0625rem 0;
  background-color: $color-grey-bg;

  h2 {
    max-width: 324px;
    line-height: 3.8112rem;
    letter-spacing: -0.06em;
  }

  .tech-we-use {
    display: flex;
    justify-content: space-between;
    margin-bottom: 6.8125rem;
    @media (max-width: $screen-md) {
      display: block;
    }

    p {
      font-family: Aileron;
      font-size: 21px;
      font-weight: 300;
      line-height: 34px;
      letter-spacing: 0.03em;
      text-align: left;
      max-width: 760px;
    }
  }

  .tech-stack-box {
    padding: 2.9375rem 2.5rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: $color-base;
    border-radius: 20px;
    @media (max-width: $screen-lg) {
      display: block;
    }
  }

  h3 {
    color: $brand-dark;
    font-family: Ranade;
    font-size: 1.1875rem;
    font-weight: 500;
    line-height: 23px;
    letter-spacing: -0.01em;
    text-align: left;
  }
}

//  Trusted Process Section

.process-sec {
  .WhyUs {
    margin-bottom: 2.5rem;
    align-items: center;
  }

  .tested-process {
    margin-bottom: 6.4375rem;
    p {
      max-width: 481px;
    }
    a {
      color: #b01f24;
      max-width: 20.75rem;
      font-family: Aileron;
      font-size: 21px;
      font-weight: 400;
      line-height: 34px;
      letter-spacing: -0.01em;
      text-align: right;
    }
  }

  .design-box {
    background-image: url("/dist/img/design-focused.png");
  }

  .dvlopment-practices {
    background-image: url("/dist/img/development-practices.png");
  }

  .quality-assurance {
    background-image: url("/dist/img/imp-quality-assurance.png");
  }
  .cloud-infra {
    background-image: url("/dist/img/cloud-infra.png");
  }

  .cloud-deploy {
    background-image: url("/dist/img/cloud-deployments.png");
  }

  .process-box {
    padding: 9.8125rem 24px 1px 24px;
    background-repeat: no-repeat;
    // background-size: cover;

    .process-cnt {
      padding: 0px 24px;
      box-shadow: 0px 4px 4px 0px #0000001f;
      background: #ffffff;
      border-radius: 12px;
      opacity: 96%;
      height: 105px;
      display: flex;
      align-items: center;

      h3 {
        max-width: 317px;
        font-family: Ranade;
        font-size: 1.3125rem;
        font-weight: 400;
        line-height: 1.4375rem;
        letter-spacing: -0.04em;
        text-align: left;
        color: #1e1e1e;
      }
    }
  }

  .process-box-1 {
    padding: 24px;
    background-image: url("/dist/img/business-centred.png");
    background-repeat: no-repeat;
    // background-size: cover;

    .process-cnt-1 {
      box-shadow: 0px 4px 4px 0px #0000001f;
      background: #ffffff;
      border-radius: 12px;
      opacity: 96%;
      padding: 28px 24px 0px 24px;

      h3 {
        max-width: 317px;
        font-family: Ranade;
        font-size: 1.3125rem;
        font-weight: 400;
        line-height: 23px;
        letter-spacing: -0.04em;
        text-align: left;
        color: #1e1e1e;
        margin-bottom: 4.625rem;
      }

      p {
        color: #1e1e1e;
        font-weight: 400;
        font-family: aileron;
        font-size: 0.875rem;
        letter-spacing: 0.02em;
        text-align: left;
        padding-bottom: 1.6875rem;
      }
    }
  }
}

//  FAQ Section

.faq-sec {
  padding: 145px 0px 171px 0px;
  background-color: #f9efef;
  background-image: url("/dist/img/faq-bg.png");
  background-repeat: no-repeat;
  background-position-y: 92%;

  .accordion {
    h2 {
      font-family: Ranade;
      font-size: 1.1875rem;
      font-weight: 500;
      line-height: 23px;
      letter-spacing: -0.01em;
      text-align: left;
    }

    .accordion-item {
      margin-bottom: 1.25rem;
      box-shadow: 0px 8px 16px 0px #b01f241f;
      border-radius: 20px;
    }
  }

  // .accordion-button:not(.collapsed){
  //   box-shadow: 0px 8px px 0px #fff;
  // }
  .collapsed {
    padding: 40px 40px;
  }

  .accordion-button {
    font-family: Ranade;
    font-size: 1.1875rem;
    font-weight: 500;
    line-height: 23px;
    letter-spacing: -0.01em;
    text-align: left;
    border-radius: 20px;
  }

  .accordion-body {
    padding: 0 48px;
    p {
      font-family: Aileron;
      font-size: 1.1875rem;
      font-weight: 300;
      line-height: 30px;
      letter-spacing: 0.03em;
      text-align: left;
    }
  }
}

.think201-icons-slider {
  .slick-slide {
    img {
      margin: 0 !important;
    }
  }
}
